import helper from '../../common/helper.js'
export default {
	data() {
		return {
			PlanArray: '',
			tishi:''

		};
	},
	onLoad() {
		this.Domaininfo();
		
	},
	onShow() {
		this.Domaininfo()
	},
	methods: {
		// 获取绑定平台信息
		Domaininfo() {
			uni.showLoading({
					title: "加载中....",
					mask: true
				}),
				uni.request({
					url: helper.websiteUrl + 'plan/userplanlist',
					method: 'GET',
					data: helper.postdata({
						"userid": helper.getstate().userid,
					}),
					success: res => {
						if (res.data.code == 200) {
							uni.hideLoading()
							this.PlanArray = res.data.data.baseinfo
							if (this.PlanArray=="") {
								this.tishi = '未绑定平台'
							} else{
								this.tishi = ''
							}
						} else {
							helper.goout(res.data.info);
							uni.showToast({
								icon: 'none',
								title: res.data.info
							});
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
		},
		// 修改绑定平台信息
		opendomain(e) {
			uni.navigateTo({
				url: 'Domaindetails?ptid=' + e.currentTarget.dataset.ptid
			});
		},
		// 添加平台信息
		adddomain() {
			uni.navigateTo({
				url: 'AddDomain',
			});
		}
	}
}
